import * as React from 'react';
// TODO: to be refacted
import Basic from '../../selectPanel';
// import '../../style';

const cityPY = [
  {
    from: 97,
    to: 103,
  },
  {
    from: 104,
    to: 110,
  },
  {
    from: 111,
    to: 116,
  },
  {
    from: 117,
    to: 122,
  },
];

const sortCity = (index: any) => (option: any) => {
  const pyCode = option.pinyin.codePointAt(0);
  return pyCode >= cityPY[index].from && pyCode <= cityPY[index].to;
};

const sortOption = (prev: any, next: any) => {
  const prevCode = prev.pinyin.codePointAt(0);
  const nextCode = next.pinyin.codePointAt(0);
  return prevCode - nextCode;
};
(window as any).__data = { loginUsername: 'admin' };

export default class HawkeyeCitySelector extends React.PureComponent<any, any> {
  basic: any
  reset = (value: any) => this.basic.reset(value);

  render() {
    const transProps = {
      tabList: [
        { value: '1', mean: '自定义' },
        { value: '2', mean: '常用', filter: (_option: any, index: any) => index < 10 },
        { value: '3', mean: 'A-G', filter: sortCity(0), sort: sortOption },
        { value: '4', mean: 'H-N', filter: sortCity(1), sort: sortOption },
        { value: '5', mean: 'O-T', filter: sortCity(2), sort: sortOption },
        { value: '6', mean: 'U-Z', filter: sortCity(3), sort: sortOption },
      ],
      defaultTab: '1',
      withRegion: false,
      showName: false,
      name: '城市',
      ...this.props,
    } as any;
    // 关于dataKey这个东西，需要考虑到数据权限， 所以建议设置businessType
    if (this.props.limited) {
      transProps.dataKey = `${(window as any).__data.loginUsername}@cityId@${this.props.limited}`;
    }
    transProps.dataKey = `$${(window as any).__data.loginUsername}@cityId`;
    return <Basic {...transProps} ref={c => (this.basic = c)} />;
  }
}
